import React from "react";
import "./About.css";
import Reveal from "../utils/Reveal/Reveal";

const About = () => {
  return (
    <div className="about-content">
      <div className="heading">
        <span className="span-text">Who Am I?</span>
      </div>
      <Reveal
        text={`I'm a skilled freelance Java developer ready to embrace the world of JavaScript! 
        With a solid background in Java and a passion for coding, I'm excited to leverage my expertise in front-end and back-end development using this dynamic language.
        I'm confident that transitioning to JavaScript will be a smooth and rewarding journey. 
        I look forward to creating interactive and user-friendly websites and applications, delivering seamless user experiences that leave a lasting impact.


        Let's work together to build cutting-edge web applications and bring your ideas to life! 
        I'm thrilled to embark on this new adventure and contribute my skills to the ever-evolving world of JavaScript.`}
      />
      <Reveal text="Email: ramilatangan@gmail.com" />
    </div>
  );
};

export default About;
