import React from "react";
import "../Work/Work.css";
import {
  VerticalTimeline,
  VerticalTimelineElement,
} from "react-vertical-timeline-component";
import "react-vertical-timeline-component/style.min.css";
import WorkIcon from "../WorkIcon/WorkIcon";
import StarIcon from "../StarIcon/StarIcon";
import SchoolIcon from "../SchoolIcon/SchoolIcon";

const Work = () => {
  return (
    <div className="work">
      <VerticalTimeline>
        <VerticalTimelineElement
          className="vertical-timeline-element--work"
          //contentStyle={{ background: "", color: "#fff" }}
          contentArrowStyle={{ borderRight: "7px solid  rgb(33, 150, 243)" }}
          date="2022 - 2022"
          dateClassName="white"
          iconStyle={{ background: "rgb(33, 150, 243)", color: "#fff" }}
          icon={<WorkIcon />}
        >
          <h3 className="vertical-timeline-element-title">
            Senior Software Engineer
          </h3>
          <h4 className="vertical-timeline-element-subtitle">
            Accenture, Inc.
          </h4>
          <p>Insurance System Support and Maintenance</p>
        </VerticalTimelineElement>

        <VerticalTimelineElement
          className="vertical-timeline-element--work"
          date="2019 - 2021"
          dateClassName="white"
          iconStyle={{ background: "rgb(33, 150, 243)", color: "#fff" }}
          icon={<WorkIcon />}
        >
          <h3 className="vertical-timeline-element-title">
            System Kaizen Staff
          </h3>
          <h4 className="vertical-timeline-element-subtitle">
            Glory Philippines Inc.
          </h4>
          <p>
            Quality Control Checksheet System, Production Time Monitoring,
            Inventory Monitoring
          </p>
        </VerticalTimelineElement>

        <VerticalTimelineElement
          className="vertical-timeline-element--work"
          date="2016 - 2017"
          dateClassName="white"
          iconStyle={{ background: "rgb(33, 150, 243)", color: "#fff" }}
          icon={<WorkIcon />}
        >
          <h3 className="vertical-timeline-element-title">Android Developer</h3>
          <h4 className="vertical-timeline-element-subtitle">Bittel Asia</h4>
          <p>
            Convert PSD design to mobile/TV platform. Design Set-top box
            applications.
          </p>
        </VerticalTimelineElement>

        <VerticalTimelineElement
          className="vertical-timeline-element--education"
          date="2016"
          dateClassName="white"
          iconStyle={{ background: "rgb(16, 204, 82)", color: "#fff" }}
          icon={<SchoolIcon />}
        >
          <h3 className="vertical-timeline-element-title">
            Bachelor of Science in Information Technology
          </h3>
          <h4 className="vertical-timeline-element-subtitle">
            STI College Rosario
          </h4>
          <p>Bachelor</p>
        </VerticalTimelineElement>

        <VerticalTimelineElement
          iconStyle={{ background: "rgb(233, 30, 99)", color: "#fff" }}
          icon={<StarIcon />}
        />
      </VerticalTimeline>
    </div>
  );
};

export default Work;
